import React, { createContext, useContext, useState, useEffect } from 'react';
import { Outlet, useLocation, useNavigate } from 'react-router-dom';

const sidebarLinks = [
  { section: 'Overview', items: ['Quickstart', 'FAQ', 'Principles', 'Models', 'Enterprise', 'Privacy Policy'] },
  {
    section: 'Features', items: [
      'ARMS Routing', 'Compliance Engine', 'Prompt Caching', 'Structured Outputs', 'Tool Calling', 'Message Transforms', 'Uptime Optimization', 'Zero Completion Insurance', 'Provisioning API Keys'
    ]
  },
  {
    section: 'API Reference', items: [
      'Overview', 'Streaming', 'Limits', 'Authentication', 'Parameters', 'Errors', 'API Keys'
    ]
  },
  {
    section: 'Use Cases', items: [
      'BYOK', 'Crypto API', 'OAuth PKCE', 'MCP Servers', 'For Providers', 'Reasoning Tokens', 'Usage Accounting', 'User Tracking'
    ]
  },
  { section: 'Community', items: ['Frameworks', 'Discord'] },
];

const slugify = (str: string) =>
  str.toLowerCase().replace(/\s+/g, '-').replace(/[^a-z0-9\-]/g, '');

// Context for On This Page links
export const DocsOnThisPageContext = createContext({
  links: [] as { label: string; anchor: string }[],
  setLinks: (_: { label: string; anchor: string }[]) => { },
});

export const useDocsOnThisPage = () => useContext(DocsOnThisPageContext);

const Docs: React.FC = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [onThisPageLinks, setOnThisPageLinks] = useState<{ label: string; anchor: string }[]>([]);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
      if (window.innerWidth >= 1024) {
        setSidebarOpen(false);
      }
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const isMobile = windowWidth < 1024;
  const isTablet = windowWidth < 1280; // Tablet/Small desktop for right sidebar logic

  return (
    <DocsOnThisPageContext.Provider value={{ links: onThisPageLinks, setLinks: setOnThisPageLinks }}>
      <div className="min-h-screen bg-slate-900 text-slate-300 font-sans">

        {/* Banner */}
        <div className="w-full bg-brand/10 text-brand-light text-center py-2 px-4 text-sm font-semibold border-b border-brand/20">
          Docs being updated. Coming soon...
        </div>

        <div className="flex max-w-[1920px] mx-auto w-full relative">

          {/* Mobile Hamburger */}
          {isMobile && (
            <button
              onClick={() => setSidebarOpen(true)}
              className="fixed bottom-6 right-6 z-50 p-4 bg-brand text-white rounded-full shadow-lg border border-brand-light hover:bg-brand-dark transition-colors"
              aria-label="Open menu"
            >
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="3" y1="12" x2="21" y2="12"></line><line x1="3" y1="6" x2="21" y2="6"></line><line x1="3" y1="18" x2="21" y2="18"></line></svg>
            </button>
          )}

          {/* Left Sidebar */}
          <aside
            className={`
                fixed lg:sticky top-0 h-screen overflow-y-auto bg-slate-900 border-r border-white/5 w-72 p-6 z-40 transition-transform duration-300 ease-in-out
                ${isMobile ? (sidebarOpen ? 'translate-x-0' : '-translate-x-full') : 'translate-x-0 lg:top-[57px] lg:h-[calc(100vh-57px)]'}
            `}
          >
            {/* Mobile Close Button & Header */}
            <div className="lg:hidden flex justify-between items-center mb-6">
              <div className="font-bold text-white text-xl flex items-center gap-3">
                <img src="/logo-small.jpeg" alt="Logo" className="w-8 h-8 rounded-lg object-cover" />
                Docs
              </div>
              <button onClick={() => setSidebarOpen(false)} className="text-slate-400 hover:text-white">
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"></line></svg>
              </button>
            </div>

            <nav className="space-y-8 pb-20 lg:pb-0">
              {sidebarLinks.map((section) => (
                <div key={section.section}>
                  <h3 className="text-xs font-bold text-slate-500 uppercase tracking-wider mb-3 px-2">
                    {section.section}
                  </h3>
                  <ul className="space-y-1">
                    {section.items.map((item) => {
                      const slug = slugify(item);
                      const isActive = location.pathname.toLowerCase().includes(slug);
                      return (
                        <li key={item}>
                          <a
                            href={`/docs/${slug}`}
                            onClick={(e) => {
                              e.preventDefault();
                              setSidebarOpen(false);
                              navigate(`/docs/${slug}`);
                            }}
                            className={`block px-3 py-2 rounded-lg text-sm transition-colors ${isActive
                                ? 'bg-brand/10 text-brand font-medium'
                                : 'text-slate-400 hover:text-slate-100 hover:bg-white/5'
                              }`}
                          >
                            {item}
                          </a>
                        </li>
                      );
                    })}
                  </ul>
                </div>
              ))}
            </nav>
          </aside>

          {/* Overlay for mobile sidebar */}
          {isMobile && sidebarOpen && (
            <div className="fixed inset-0 bg-black/50 z-30" onClick={() => setSidebarOpen(false)} />
          )}

          {/* Main Content */}
          <main className="flex-1 min-w-0 py-10 px-4 sm:px-8 lg:px-12 xl:px-16 w-full max-w-5xl mx-auto">
            <div className="prose prose-invert prose-slate max-w-none">
              <Outlet />
            </div>
          </main>

          {/* Right Sidebar (On This Page) */}
          {!isTablet && onThisPageLinks.length > 0 && (
            <aside className="hidden xl:block w-64 sticky top-[57px] h-[calc(100vh-57px)] overflow-y-auto p-6 border-l border-white/5">
              <h4 className="text-sm font-bold text-white mb-4 uppercase tracking-wider">On this page</h4>
              <ul className="space-y-2 text-sm border-l border-white/10">
                {onThisPageLinks.map((item) => (
                  <li key={item.anchor} className="-ml-px">
                    <a
                      href={`#${item.anchor}`}
                      className="block pl-4 py-1 text-slate-500 hover:text-brand hover:border-l-2 hover:border-brand transition-all border-l-2 border-transparent"
                    >
                      {item.label}
                    </a>
                  </li>
                ))}
              </ul>
            </aside>
          )}

        </div>
      </div>
    </DocsOnThisPageContext.Provider>
  );
};

export default Docs;