import React, { useState } from 'react';

const Footer: React.FC = () => {
  const [form, setForm] = useState({
    name: '',
    designation: '',
    email: '',
    phone: '',
    message: '',
  });
  const [submitting, setSubmitting] = useState(false);
  const [success, setSuccess] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitting(true);
    setSuccess(null);
    setError(null);
    try {
      const response = await fetch('https://backend.tilantra.com/inquiry', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(form),
      });
      if (response.ok) {
        setSuccess('Thank you for your inquiry!');
        setForm({ name: '', designation: '', email: '', phone: '', message: '' });
      } else {
        const err = await response.json();
        setError('Error: ' + (err.detail || 'Could not submit inquiry.'));
      }
    } catch (error) {
      setError('Network error. Please try again later.');
    }
    setSubmitting(false);
  };

  return (
    <footer className="bg-slate-900 text-slate-300 border-t border-white/5 pt-16 pb-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12 mb-12">

          {/* Brand Column */}
          <div className="space-y-6">
            <div className="flex items-center">
              {/* Logo - assuming white version or filter */}
              <img src="/Tilantra_blueLOGO.png" alt="Tilantra Logo" className="h-10 brightness-0 invert" />
            </div>
            <p className="text-slate-400 leading-relaxed text-sm">
              High-performance orchestration for LLMs.<br />
              Total control, compliance, and cost-performance.
            </p>
          </div>

          {/* Quick Links */}
          <div>
            <h4 className="text-white font-bold text-lg mb-6">Quick Links</h4>
            <ul className="space-y-4 text-sm font-medium">
              {['Home', 'Docs', 'About', 'Features'].map((item) => (
                <li key={item}>
                  <a
                    href={item === 'Home' ? '/' : item === 'Docs' ? '/docs' : `#${item.toLowerCase() === 'features' ? 'our-features' : 'what-we-are'}`}
                    className="text-slate-400 hover:text-white transition-colors"
                  >
                    {item}
                  </a>
                </li>
              ))}
            </ul>
          </div>

          {/* Contact Info */}
          <div>
            <h4 className="text-white font-bold text-lg mb-6">Contact</h4>
            <ul className="space-y-4 text-sm text-slate-400">
              <li className="flex items-center gap-3">
                <svg className="w-5 h-5 text-brand" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z" /><polyline points="22,6 12,13 2,6" /></svg>
                tech@tilantra.com
              </li>
              <li className="flex items-center gap-3">
                <svg className="w-5 h-5 text-brand" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M22 16.92v3a2 2 0 0 1-2.18 2 19.79 19.79 0 0 1-8.63-3.07 19.5 19.5 0 0 1-6-6 19.79 19.79 0 0 1-3.07-8.67A2 2 0 0 1 4.11 2h3a2 2 0 0 1 2 1.72 12.84 12.84 0 0 0 .7 2.81 2 2 0 0 1-.45 2.11L8.09 9.91a16 16 0 0 0 6 6l1.27-1.27a2 2 0 0 1 2.11-.45 12.84 12.84 0 0 0 2.81.7A2 2 0 0 1 22 16.92z" /></svg>
                +91 7042644614
              </li>
              <li className="flex items-center gap-3">
                <svg className="w-5 h-5 text-brand" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M12 21s-6-5.686-6-10A6 6 0 0 1 18 11c0 4.314-6 10-6 10z" /><circle cx="12" cy="11" r="2.5" /></svg>
                Bangalore, IN
              </li>
              <li>
                <a href="https://www.linkedin.com/company/tilantra" target="_blank" rel="noopener noreferrer" className="flex items-center gap-3 hover:text-white transition-colors">
                  <svg className="w-5 h-5" viewBox="0 0 24 24" fill="#0A66C2"><path d="M12 0c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm-2 16h-2v-6h2v6zm-1-6.891c-.607 0-1.1-.496-1.1-1.109 0-.612.492-1.109 1.1-1.109s1.1.497 1.1 1.109c0 .613-.493 1.109-1.1 1.109zm8 6.891h-1.998v-2.861c0-1.881-2.002-1.722-2.002 0v2.861h-2v-6h2v1.093c.872-1.616 4-1.736 4 1.548v3.359z" /></svg>
                  LinkedIn
                </a>
              </li>
            </ul>
          </div>

          {/* Contact Form */}
          <div className="lg:col-span-1">
            <h4 className="text-white font-bold text-lg mb-6">Schedule a Full Demo</h4>
            <form onSubmit={handleSubmit} className="bg-slate-800 p-6 rounded-2xl border border-white/10 space-y-4 shadow-lg">
              <div className="grid grid-cols-2 gap-3">
                <div>
                  <label htmlFor="name" className="block text-xs font-medium text-slate-400 mb-1">Name <span className="text-red-400">*</span></label>
                  <input type="text" id="name" name="name" value={form.name} onChange={handleChange} required className="w-full bg-slate-900 border border-white/10 rounded-lg px-3 py-2 text-sm text-white focus:ring-2 focus:ring-brand focus:border-transparent outline-none transition-all" />
                </div>
                <div>
                  <label htmlFor="designation" className="block text-xs font-medium text-slate-400 mb-1">Role</label>
                  <input type="text" id="designation" name="designation" value={form.designation} onChange={handleChange} className="w-full bg-slate-900 border border-white/10 rounded-lg px-3 py-2 text-sm text-white focus:ring-2 focus:ring-brand focus:border-transparent outline-none transition-all" />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-3">
                <div>
                  <label htmlFor="email" className="block text-xs font-medium text-slate-400 mb-1">Email <span className="text-red-400">*</span></label>
                  <input type="email" id="email" name="email" value={form.email} onChange={handleChange} required className="w-full bg-slate-900 border border-white/10 rounded-lg px-3 py-2 text-sm text-white focus:ring-2 focus:ring-brand focus:border-transparent outline-none transition-all" />
                </div>
                <div>
                  <label htmlFor="phone" className="block text-xs font-medium text-slate-400 mb-1">Phone</label>
                  <input type="tel" id="phone" name="phone" value={form.phone} onChange={handleChange} className="w-full bg-slate-900 border border-white/10 rounded-lg px-3 py-2 text-sm text-white focus:ring-2 focus:ring-brand focus:border-transparent outline-none transition-all" />
                </div>
              </div>

              <div>
                <label htmlFor="message" className="block text-xs font-medium text-slate-400 mb-1">Message <span className="text-red-400">*</span></label>
                <textarea id="message" name="message" rows={3} value={form.message} onChange={handleChange} required className="w-full bg-slate-900 border border-white/10 rounded-lg px-3 py-2 text-sm text-white focus:ring-2 focus:ring-brand focus:border-transparent outline-none transition-all resize-none"></textarea>
              </div>

              <button type="submit" disabled={submitting} className="w-full btn-primary text-sm font-semibold disabled:opacity-50 disabled:cursor-not-allowed">
                {submitting ? 'Submitting...' : 'Submit Inquiry'}
              </button>
              {success && <div className="text-green-400 text-xs mt-2 text-center">{success}</div>}
              {error && <div className="text-red-400 text-xs mt-2 text-center">{error}</div>}
            </form>
          </div>

        </div>

        <div className="border-t border-white/5 pt-8 text-center">
          <p className="text-slate-500 text-sm"> 2025 Tilantra Technologies. All Rights Reserved.</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;