import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

const DEMO_EXAMPLES = [
  {
    prompt: "How do I optimize a SQL query for faster performance?",
    response: "To optimize a SQL query:\n\n- Use proper indexing on columns used in WHERE, JOIN, and ORDER BY clauses.\n- Avoid using SELECT *; specify only the columns you need.\n- Limit the use of subqueries and try to use JOINs instead.\n- Analyze the query execution plan to identify bottlenecks.\n\nIf you share your specific query, I can provide more targeted suggestions!",
    model: "Ollama o4-mini",
    compliance: {
      plagiarism: "0%",
      thirdParty: "None",
      policyViolated: false,
      policyReason: "",
      status: "PASSED"
    }
  },
  {
    prompt: "Why does my Docker container exit immediately after starting?",
    response: "A Docker container may exit immediately if:\n\n- Its main process finishes right after starting (e.g., the command in CMD/ENTRYPOINT completes).\n- There is an error in the entrypoint script or command.\n- The container is running in the background and has nothing to keep it alive.\n\nCheck your Dockerfile's CMD/ENTRYPOINT and run 'docker logs <container>' for more details.",
    model: "Qwen-1.5-72B",
    compliance: {
      plagiarism: "0%",
      thirdParty: "None",
      policyViolated: false,
      policyReason: "",
      status: "PASSED"
    }
  },
  {
    prompt: "What are the main arguments for and against universal basic income?",
    response: "I'm sorry, but I can't assist with that request.",
    model: "Claude 3 Sonnet",
    compliance: {
      plagiarism: "0%",
      thirdParty: "None",
      policyViolated: true,
      policyReason: "User requested political content, which is not permitted by company policy.",
      status: "FAILED"
    }
  },
  {
    prompt: "What should I do if I have a persistent cough and fever?",
    response: "I'm not able to provide medical advice. Please consult a healthcare professional for your symptoms.",
    model: "Gemini 1.5 Pro",
    compliance: {
      plagiarism: "0%",
      thirdParty: "None",
      policyViolated: true,
      policyReason: "User requested medical advice, which is not permitted by company policy.",
      status: "FAILED"
    }
  },
  {
    prompt: "Give me 5 creative startup ideas for 2024 in the AI space.",
    response: "Here are 5 creative AI startup ideas for 2024:\n\n1. AI-powered mental health assistant for real-time emotional support.\n2. Automated legal document analyzer for contract review.\n3. Personalized AI fitness coach with adaptive workout plans.\n4. AI-driven supply chain optimizer for small businesses.\n5. Real-time AI content moderation tool for online communities.",
    model: "GPT-4o",
    compliance: {
      plagiarism: "23%",
      thirdParty: "Some ideas overlap with public startup lists.",
      policyViolated: false,
      policyReason: "",
      status: "PASSED"
    }
  },
  {
    prompt: "I want to draft an email for m@gmail.com and a@gmail.com.",
    response: "Sure! Here's a draft email for your recipients:\n\nSubject: [Your Subject Here]\n\nHi email@gmail.com and email@gmail.com,\n\n[Your message here]\n\nBest regards,\n[Your Name]",
    model: "Yi-34B-Chat",
    compliance: {
      plagiarism: "12%",
      thirdParty: "Email format and phrasing similar to common templates.",
      policyViolated: false,
      policyReason: "Personal information (emails) replaced with placeholders for privacy.",
      status: "PASSED"
    }
  }
];

interface Message {
  sender: 'user' | 'bot';
  text: string;
  model?: string;
  compliance?: {
    plagiarism: string;
    thirdParty: string;
    policyViolated: boolean;
    policyReason: string;
    status: string;
  };
}

const ChatbotSection: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [loadingStep, setLoadingStep] = useState(0);

  const handlePrompt = async (exampleIdx: number) => {
    const example = DEMO_EXAMPLES[exampleIdx];
    setMessages(msgs => [
      ...msgs,
      { sender: 'user', text: example.prompt },
    ]);
    setLoadingStep(1);
    setTimeout(() => {
      setLoadingStep(0);
      setTimeout(() => {
        setLoadingStep(2);
        setTimeout(() => {
          setLoadingStep(0);
          setMessages(msgs => [
            ...msgs,
            {
              sender: 'bot',
              text: example.compliance && example.compliance.policyViolated
                ? 'Compliance check failed. Please try again.'
                : example.response,
              model: example.compliance && example.compliance.policyViolated ? undefined : example.model,
              compliance: example.compliance,
            },
          ]);
        }, 1500);
      }, 200);
    }, 1500);
  };

  return (
    <section id="tilantra-assistant" className="section-padding py-24 bg-slate-900 border-t border-white/5">
      <div className="bg-slate-800/50 backdrop-blur-sm border border-white/10 rounded-3xl p-6 md:p-10 flex flex-col lg:flex-row gap-10 shadow-2xl">

        {/* Chat Area */}
        <div className="flex-1 lg:flex-[2] flex flex-col h-[600px] border-r border-white/10 pr-0 lg:pr-8">
          <div className="mb-6">
            <h2 className="text-3xl font-extrabold tracking-tight mb-2">
              <span className="text-white">Experience the </span>
              <span className="bg-clip-text text-transparent bg-gradient-to-r from-brand to-accent-teal animate-pulse">
                Future of AI
              </span>
            </h2>
            <p className="text-brand font-medium">
              This is a basic demo, for the full Demo <a href="/chatbot" className="underline hover:text-white transition-colors">Click Here</a>.
            </p>
          </div>

          <div className="flex-1 overflow-y-auto bg-slate-900/50 rounded-xl p-6 border border-white/5 space-y-6 scrollbar-thin scrollbar-thumb-slate-700">
            <AnimatePresence initial={false}>
              {messages.map((msg, i) => (
                <motion.div
                  key={i}
                  initial={{ opacity: 0, y: 10, scale: 0.95 }}
                  animate={{ opacity: 1, y: 0, scale: 1 }}
                  className={`flex flex-col ${msg.sender === 'user' ? 'items-end' : 'items-start'}`}
                >
                  <div
                    className={`max-w-[85%] rounded-2xl p-4 text-sm md:text-base shadow-lg border relative ${msg.sender === 'user'
                        ? 'bg-brand text-white border-brand-light/50 rounded-tr-sm'
                        : 'bg-slate-800 text-slate-200 border-white/10 rounded-tl-sm'
                      }`}
                  >
                    {msg.sender === 'bot' && msg.compliance && !msg.compliance.policyViolated && (
                      <div className="text-xs font-bold text-accent-teal mb-2 uppercase tracking-wider">
                        Model: {msg.model || 'Demo Mode'}
                      </div>
                    )}
                    <p className="whitespace-pre-wrap">{msg.text}</p>

                    {/* Compliance Report */}
                    {msg.sender === 'bot' && msg.compliance && (
                      <div className="mt-4 pt-3 border-t border-white/10 text-xs">
                        <div className={`font-bold mb-1 ${msg.compliance.status === 'PASSED' ? 'text-green-400' : 'text-red-400'}`}>
                          Compliance Report
                        </div>
                        {msg.compliance.policyReason && msg.compliance.policyReason.toLowerCase().includes('placeholders for privacy') && (
                          <div className="bg-green-500/10 text-green-400 p-2 rounded mb-2 border border-green-500/20">
                            Personal information found. Replaced with placeholder.
                          </div>
                        )}
                        <div className={`p-3 rounded bg-slate-900 border ${msg.compliance.status === 'PASSED' ? 'border-green-500/20 text-green-300' : 'border-red-500/20 text-red-300'}`}>
                          <p>Plagiarism: {msg.compliance.plagiarism}</p>
                          <p>Third Party: {msg.compliance.thirdParty}</p>
                          <p>Violation: {msg.compliance.policyViolated ? 'TRUE' : 'FALSE'}</p>
                          {msg.compliance.policyViolated && <p>Reason: {msg.compliance.policyReason}</p>}
                          <p className="font-bold mt-1">Status: {msg.compliance.status}</p>
                        </div>
                      </div>
                    )}
                  </div>
                </motion.div>
              ))}
            </AnimatePresence>

            {/* Loading Indicators */}
            {loadingStep === 1 && (
              <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="flex items-center gap-3 text-brand-light">
                <div className="w-5 h-5 border-2 border-current border-t-transparent rounded-full animate-spin"></div>
                <span className="font-medium text-sm">Choosing the Best Model...</span>
              </motion.div>
            )}
            {loadingStep === 2 && (
              <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="flex items-center gap-3 text-accent-teal">
                <div className="w-5 h-5 border-2 border-current border-t-transparent rounded-full animate-spin"></div>
                <span className="font-medium text-sm">Generating Compliance Report...</span>
              </motion.div>
            )}
          </div>
        </div>

        {/* Sidebar Prompts */}
        <div className="lg:w-1/3 flex flex-col gap-6 pt-0 lg:pt-2">
          <h3 className="text-xl font-bold text-brand uppercase tracking-wider">Demo Prompts</h3>
          <div className="flex flex-col gap-3 overflow-y-auto max-h-[500px] pr-2 scrollbar-thin scrollbar-thumb-slate-700">
            {DEMO_EXAMPLES.map((ex, i) => (
              <button
                key={i}
                onClick={() => handlePrompt(i)}
                disabled={loadingStep !== 0}
                className="text-left p-4 rounded-xl bg-slate-800 border border-white/5 hover:border-brand/50 hover:bg-slate-700 transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed group shadow-sm hover:shadow-brand/10"
              >
                <p className="text-slate-300 font-medium text-sm group-hover:text-white line-clamp-2">{ex.prompt}</p>
              </button>
            ))}
          </div>
        </div>

      </div>
    </section>
  );
};

export default ChatbotSection;