const UseCasesSection: React.FC = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [isMobile, setIsMobile] = useState(false);
  const sectionRefs = useRef<(HTMLDivElement | null)[]>([]);
  const scrollContainerRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    const checkMobile = () => setIsMobile(window.matchMedia('(max-width: 1024px)').matches);
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  useEffect(() => {
    if (!isMobile) {
      const handleScroll = () => {
        const offsets = sectionRefs.current.map(ref => ref?.getBoundingClientRect().top ?? Infinity);
        // Determine active index based on which section is closest to top (but not too far up)
        const topOffset = window.innerHeight * 0.3;
        const active = offsets.findIndex(offset => offset >= -200 && offset < window.innerHeight * 0.6);

        if (active !== -1 && active !== activeIndex) {
          setActiveIndex(active);
        }
      };
      const container = scrollContainerRef.current;
      if (container) {
        container.addEventListener('scroll', handleScroll, { passive: true });
      }
      return () => {
        if (container) container.removeEventListener('scroll', handleScroll);
      };
    }
  }, [isMobile, activeIndex]);

  const current = useCases[activeIndex];

  return (
    <section className="bg-slate-900 text-white relative flex flex-col lg:flex-row min-h-[80vh] overflow-hidden">

      {/* Left: Sticky Protection Solution Panel */}
      <div
        className={`w-full lg:w-5/12 lg:h-[80vh] flex flex-col justify-center items-center p-8 lg:p-12 z-20 transition-colors duration-500
            ${isMobile ? 'bg-brand' : 'lg:sticky lg:top-0 bg-gradient-to-br from-brand to-brand-dark'}
        `}
      >
        <AnimatePresence mode='wait'>
          <motion.div
            key={activeIndex}
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
            transition={{ duration: 0.4 }}
            className="text-center max-w-md"
          >
            <h3 className="text-3xl lg:text-4xl font-extrabold mb-6 leading-tight">
              {current.solutionHeading}
            </h3>
            <div className="space-y-4 mb-8 text-lg font-medium text-blue-50">
              {current.protection.map((point, idx) => (
                <div key={idx} className="flex items-center justify-center gap-2">
                  <span className="text-accent-teal"></span> {point}
                </div>
              ))}
            </div>

            <div className="flex gap-4 justify-center">
              <div className="bg-white/10 backdrop-blur-md rounded-2xl p-4 min-w-[120px] flex flex-col items-center justify-center border border-white/10">
                <span className="text-sm text-blue-100 font-bold uppercase tracking-wider mb-2">{current.statBoxText}</span>
                {/* Placeholder icon if img fails, or use img */}
                {/* <div className="text-3xl"></div>  */}
                <img src={current.statBoxLogo} alt="" className="w-10 h-10 object-contain invert brightness-0" />
              </div>
            </div>
          </motion.div>
        </AnimatePresence>
      </div>

      {/* Right: Scrollable Use Cases */}
      <div
        ref={scrollContainerRef}
        className="w-full lg:w-7/12 h-auto lg:h-[80vh] overflow-y-auto snap-y snap-mandatory bg-slate-900 scroll-smooth"
      >
        {useCases.map((uc, i) => (
          <div
            key={i}
            ref={el => { sectionRefs.current[i] = el; }}
            className="w-full min-h-[60vh] lg:min-h-[80vh] flex flex-col justify-center items-center p-8 lg:p-16 box-border snap-start relative border-b border-white/5 lg:border-none"
          >
            <div className="max-w-xl text-center">
              <h3 className="text-3xl lg:text-4xl font-bold text-white mb-6">
                {uc.title}
              </h3>
              <p className="text-lg text-slate-300 leading-relaxed mb-8">
                {uc.description}
              </p>

              <div className="bg-red-500/10 border border-red-500/20 rounded-xl p-6 text-left">
                <h4 className="text-red-400 font-bold mb-3 uppercase text-sm tracking-wider">Impact of Failure</h4>
                <ul className="space-y-2">
                  {uc.impact.map((imp, idx) => (
                    <li key={idx} className="flex items-start gap-2 text-slate-300 text-sm">
                      <span className="text-red-400 mt-1"></span>
                      {imp}
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default UseCasesSection;