import React, { useEffect, useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';

const SECTION_IDS = {
  about: 'what-we-are',
  features: 'our-features',
  contact: 'contact-footer',
  hero: 'hero-section',
};

const scrollToId = (id: string) => {
  const el = document.getElementById(id);
  if (el) {
    el.scrollIntoView({ behavior: 'smooth' });
  }
};

const NavBar: React.FC = () => {
  const [scrolled, setScrolled] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const location = useLocation();
  const navigate = useNavigate();
  const [pendingScroll, setPendingScroll] = useState<string | null>(null);

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 10);
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  useEffect(() => {
    setMobileMenuOpen(false);
  }, [location]);

  useEffect(() => {
    if (pendingScroll) {
      // Wait for DOM to update
      setTimeout(() => {
        scrollToId(pendingScroll);
        // If element still not found (e.g. slight delay in rendering), try once more
        setTimeout(() => scrollToId(pendingScroll), 100);
        setPendingScroll(null);
      }, 100);
    }
  }, [location, pendingScroll]);

  const handleNavClick = (e: React.MouseEvent, id: string) => {
    e.preventDefault();
    if (location.pathname === "/") {
      scrollToId(id);
    } else {
      setPendingScroll(id);
      navigate("/");
    }
    setMobileMenuOpen(false);
  };

  return (
    <nav className={`fixed top-0 w-full z-50 transition-all duration-300 ${scrolled ? 'bg-slate-900/80 backdrop-blur-md border-b border-white/10 py-4 shadow-lg' : 'bg-transparent py-6'}`}>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 flex items-center justify-between">

        {/* Logo */}
        <div className="flex-shrink-0">
          <a href="#hero-section" onClick={(e) => handleNavClick(e, SECTION_IDS.hero)} className="block">
            {/* Using bright filter for dark mode compatibility if it's a dark logo, or ideally use text/svg */}
            <img src="/Tilantra_blueLOGO.png" alt="Tilantra Logo" className="h-10 w-auto brightness-0 invert" />
            {/* Fallback to text if logo looks bad inverted: */}
            {/* <span className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-teal-400">Tilantra</span> */}
          </a>
        </div>

        {/* Desktop Menu */}
        <div className="hidden md:flex items-center space-x-8">
          {['About', 'Features', 'Contact'].map((item) => (
            <a
              key={item}
              href={`#${SECTION_IDS[item.toLowerCase() as keyof typeof SECTION_IDS]}`}
              onClick={(e) => handleNavClick(e, SECTION_IDS[item.toLowerCase() as keyof typeof SECTION_IDS])}
              className="text-sm font-medium text-slate-300 hover:text-white transition-colors"
            >
              {item}
            </a>
          ))}
          <Link to="/docs" className="text-sm font-medium text-slate-300 hover:text-white transition-colors">
            Docs
          </Link>
          <button
            onClick={() => {
              if (location.pathname === "/") scrollToId('tilantra-assistant');
              else {
                setPendingScroll('tilantra-assistant');
                navigate("/");
              }
            }}
            className="btn-primary"
          >
            Get a Demo
          </button>
        </div>

        {/* Mobile menu button */}
        <div className="md:hidden flex items-center">
          <button
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            className="text-slate-300 hover:text-white p-2"
          >
            <div className="space-y-1.5">
              <span className={`block w-6 h-0.5 bg-current transition-transform ${mobileMenuOpen ? 'rotate-45 translate-y-2' : ''}`} />
              <span className={`block w-6 h-0.5 bg-current transition-opacity ${mobileMenuOpen ? 'opacity-0' : ''}`} />
              <span className={`block w-6 h-0.5 bg-current transition-transform ${mobileMenuOpen ? '-rotate-45 -translate-y-2' : ''}`} />
            </div>
          </button>
        </div>
      </div>

      {/* Mobile Menu Dropdown */}
      <div className={`md:hidden absolute top-full left-0 w-full bg-slate-900 border-b border-white/10 shadow-xl transition-all duration-300 origin-top ${mobileMenuOpen ? 'opacity-100 scale-y-100' : 'opacity-0 scale-y-0 pointer-events-none'}`}>
        <div className="px-4 py-6 space-y-4 flex flex-col items-start">
          {['About', 'Features', 'Contact'].map((item) => (
            <a
              key={item}
              href={`#${SECTION_IDS[item.toLowerCase() as keyof typeof SECTION_IDS]}`}
              onClick={(e) => handleNavClick(e, SECTION_IDS[item.toLowerCase() as keyof typeof SECTION_IDS])}
              className="text-lg font-medium text-slate-300 hover:text-white w-full"
            >
              {item}
            </a>
          ))}
          <Link to="/docs" className="text-lg font-medium text-slate-300 hover:text-white w-full">
            Docs
          </Link>
          <button
            onClick={() => {
              if (location.pathname === "/") scrollToId('tilantra-assistant');
              else {
                setPendingScroll('tilantra-assistant');
                navigate("/");
              }
              setMobileMenuOpen(false);
            }}
            className="btn-primary w-full justify-center mt-4"
          >
            Get a Demo
          </button>
        </div>
      </div>
    </nav>
  );
};

export default NavBar;