import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay, EffectCoverflow } from 'swiper/modules';
import { motion } from 'framer-motion';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/effect-coverflow';

const features = [
  { icon: '', title: 'Smart Routing', desc: 'Auto-selects best models based on cost, performance, and preference.', metric: '90% accuracy' },
  { icon: '', title: 'Compliance Layer', desc: 'On-demand reports & enterprise-grade governance', metric: 'SOC2 Compliant' },
  { icon: '', title: 'Secure Caching', desc: 'Reuse prompts, slash token usage.', metric: '70% Cost Savings' },
  { icon: '', title: 'Prompt Suggestions', desc: 'AI-generated prompts to optimize your inputs.', metric: '2x Productivity' },
  { icon: '', title: 'Multi-Model Hub', desc: 'Access a wide range of top AI models, all in one place.', metric: '40+ Hosted Models' },
  { icon: '', title: 'Cost Guardrails', desc: 'Real-time token budgeting to prevent overspend.', metric: 'Zero Runaway Costs' },
];

const FeaturesCarousel: React.FC = () => {
  const [activeIndex, setActiveIndex] = useState(0);

  return (
    <section className="section-padding py-24 bg-slate-900 overflow-hidden">
      <motion.div
        initial={{ opacity: 0, y: 30 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true }}
        transition={{ duration: 0.8 }}
        className="text-center mb-16"
      >
        <h2 className="text-3xl md:text-5xl font-bold text-white mb-4">
          Unlock Limitless <span className="text-brand">Possibilities</span>
        </h2>
        <p className="text-slate-400 text-lg max-w-2xl mx-auto">
          Everything you need to scale your AI operations efficiently and securely.
        </p>
      </motion.div>

      <div className="relative max-w-6xl mx-auto px-4">
        <Swiper
          modules={[Navigation, Pagination, Autoplay, EffectCoverflow]}
          spaceBetween={30}
          slidesPerView={1}
          centeredSlides={true}
          loop={true}
          autoplay={{
            delay: 3500,
            disableOnInteraction: false,
          }}
          pagination={{ clickable: true }}
          navigation={true}
          breakpoints={{
            640: {
              slidesPerView: 2,
              spaceBetween: 20,
            },
            1024: {
              slidesPerView: 3,
              spaceBetween: 30,
            },
          }}
          effect={'coverflow'}
          coverflowEffect={{
            rotate: 0,
            stretch: 0,
            depth: 100,
            modifier: 2.5,
            slideShadows: false,
          }}
          onSlideChange={(swiper: any) => setActiveIndex(swiper.realIndex)}
          className="pb-16 pt-8"
        >
          {features.map((f, i) => (
            <SwiperSlide key={i} className="h-auto">
              <div className={`h-full bg-slate-800 rounded-3xl p-8 border hover:border-brand/50 transition-all duration-300 flex flex-col items-center text-center shadow-xl ${activeIndex === i ? 'border-brand scale-105 shadow-brand/20' : 'border-white/5 opacity-80 scale-100'}`}>
                <div className="text-5xl mb-6">{f.icon}</div>
                <h3 className="text-2xl font-bold text-white mb-3">{f.title}</h3>
                <p className="text-slate-400 mb-6 flex-grow leading-relaxed">
                  {f.desc}
                </p>
                <div className="mt-auto pt-6 w-full border-t border-white/10">
                  <span className="block text-sm text-brand-light font-bold uppercase tracking-wider">
                    {f.metric}
                  </span>
                </div>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>

        {/* Custom CSS for swiper pagination bullets since they are usually blue/theme based */}
        <style>{`
            .swiper-pagination-bullet {
                background: #4b5563;
                opacity: 1;
            }
            .swiper-pagination-bullet-active {
                background: #635bff;
                width: 24px;
                border-radius: 4px;
            }
            .swiper-button-next, .swiper-button-prev {
                color: #635bff;
            }
        `}</style>
      </div>
    </section>
  );
};

export default FeaturesCarousel;